import React, { useEffect, useState } from 'react'
import './index.scss'
import { Form, Input, Button, Modal, message, Empty } from 'antd'
import {
  getUserToken,
  getCameraListUrl,
  getUpdateCameraInfo,
  pushCameraUrl,
  getDeleteCameraUrl,
} from '../../../api/api'
import axios from 'axios'

const CameraSetting = ({ visible, onCreate, onCancel, data }) => {
  const [form] = Form.useForm()
  setTimeout(() => {
    data.userName ? form.setFieldsValue({ ...data }) : form.resetFields()
  }, 300)
  return (
    <Modal
      forceRender
      visible={visible}
      title=""
      onCancel={onCancel}
      okText=""
      cancelText=""
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            form.resetFields()
            onCreate(values)
          })
          .catch(info => {})
      }}
    >
      <Form form={form} name="basic" initialValues={{ remember: true }}>
        <Form.Item
          label=""
          name="userName"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="userPassword"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="ipAddress"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="portAddress"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}

const StepThree = () => {
  axios.defaults.headers.common['Authorization'] = getUserToken()

  const [cameraList, setCameraList] = useState([])

  useEffect(() => {
    getCameraList()
  }, [])

  const getCameraList = async () => {
    const result = await axios.get(getCameraListUrl())
    if (result.data.code === '10000') {
      setCameraList(result.data.result)
    }
  }

  const [cameraVisible, setCameraVisible] = useState(false)
  const onSubmit = values => {
    axios
      .post(getUpdateCameraInfo(), {
        id: curData.id ? curData.id : null,
        ...values,
      })
      .then(res => {
        if (res.data.code === '10000') {
          setCameraVisible(false)
          setTimeout(() => {
            getCameraList()
          }, 300)
          if (curData.userName) {
            message.success(``)
          } else {
            message.success(``)
          }
        } else {
          if (curData.userName) {
            message.error(``)
          } else {
            message.error(``)
          }
          setCameraVisible(false)
        }
      })
  }

  const handleDelete = id => {
    axios.delete(getDeleteCameraUrl(id)).then(res => {
      if (res.data.code === '10000') {
        setCameraVisible(false)
        getCameraList()
        message.success(``)
      }
    })
  }

  const handleStart = id => {
    axios.get(pushCameraUrl(id)).then(res => {
      if (res.data.code === '20002') {
        message.success(``)
      }
      setTimeout(() => {
        getCameraList()
      }, 300)
    })
  }

  const handleEnd = id => {
    axios.delete(pushCameraUrl(id)).then(res => {
      if (res.data.code === '10000') {
        message.success(``)
      }
      setTimeout(() => {
        getCameraList()
      }, 300)
    })
  }

  const [curData, setCurData] = useState({})

  const open = () => {
    setCurData({})
    setCameraVisible(true)
  }

  const edit = item => {
    setCurData(item)
    setCameraVisible(true)
  }

  return (
    <div className="tab-three">
      <div className="btn-groups">
        <Button
          type="primary"
          onClick={() => {
            open()
          }}
        >
          
        </Button>
      </div>
      <div className="table-header">
        <span className="name"></span>
        <span className="password"></span>
        <span className="ip-address"></span>
        <span className="port-address"></span>
        <span className="state"></span>
        <span className="edit-box"></span>
      </div>
      <ul className="user-list">
        {cameraList.length === 0 ? (
          <Empty />
        ) : (
          cameraList.map(item => (
            <li key={item.id}>
              <span className="name">{item.userName}</span>
              <span className="password">****</span>
              <span className="ip-address">{item.ipAddress}</span>
              <span className="port-address">{item.portAddress}</span>
              <span className="state">
                {item.status === 0 ? '' : ''}
              </span>
              <span className="edit-box">
                <Button
                  onClick={() => {
                    handleStart(item.id)
                  }}
                  type="primary"
                >
                  
                </Button>
                <Button
                  onClick={() => {
                    handleEnd(item.id)
                  }}
                  type="primary"
                >
                  
                </Button>
                <Button
                  onClick={() => {
                    edit(item)
                  }}
                  type="primary"
                >
                  
                </Button>
                <Button
                  onClick={() => {
                    handleDelete(item.id)
                  }}
                  type="primary"
                >
                  
                </Button>
              </span>
            </li>
          ))
        )}
      </ul>
      <CameraSetting
        visible={cameraVisible}
        onCreate={onSubmit}
        onCancel={() => {
          setCameraVisible(false)
        }}
        data={curData}
      />
    </div>
  )
}

export default StepThree
