import React, { useState } from 'react'
import './index.scss'
import { Modal, Form, Input, message, Button, DatePicker } from 'antd'
import IconFont from '../../common/IconFont/index'
import axios from 'axios'
import { getRegisterUrl, getDeleteUrl, getUserToken, getSceneId } from '../../../api/api'
import 'moment/locale/zh-cn'
import locale from 'antd/es/date-picker/locale/zh_CN'
import moment from 'moment'

const CollectionCreateForm = ({ visible, onCreate, onCancel, data }) => {
  const [form] = Form.useForm()
  const handlePreinstall = () => {
    const now = moment().locale('zh-cn').format('YYYY-MM-DD HH:mm:ss')
    form.setFieldsValue({
      courseName: '',
      startTime: moment(`${now}`, 'YYYY-MM-DD HH:mm:ss'),
      endTime: moment(`${now}`, 'YYYY-MM-DD HH:mm:ss'),
      name: '',
      studentCount: '25',
    })
  }
  return (
    <Modal
      visible={visible}
      title=""
      onCancel={onCancel}
      okText=""
      cancelText=""
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            form.resetFields()
            onCreate(values)
          })
          .catch(info => {})
      }}
    >
      <Form form={form} name="basic" initialValues={{ remember: true }}>
        <Button
          size="small"
          type="primary"
          className="preinstall"
          onClick={() => handlePreinstall()}
        >
          
        </Button>
        <Form.Item
          label=""
          name="courseName"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="startTime"
          rules={[{ required: true, message: '' }]}
        >
          <DatePicker locale={locale} showTime />
        </Form.Item>
        <Form.Item
          label=""
          name="endTime"
          rules={[{ required: true, message: '' }]}
        >
          <DatePicker locale={locale} showTime />
        </Form.Item>
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="studentCount"
          rules={[
            {
              required: true,
              pattern: new RegExp(/^[1-9]\d*$/, 'g'),
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}

const EducationControl = props => {
  axios.defaults.headers.common['Authorization'] = getUserToken()
  const [visible, setVisible] = useState(false)

  const onCreate = values => {
    const data = {
      ...values,
      cardId: props.data.cardId,
      startTime: values.startTime.format('YYYY-MM-DD HH:mm:ss'),
      endTime: values.endTime.format('YYYY-MM-DD HH:mm:ss'),
    }
    axios
      .post(getRegisterUrl(), {
        type: getSceneId().education,
        value: {
          ...data,
        },
        cardId: props.data.cardId,
      })
      .then(res => {
        if (res.data.code === '10000') {
          message.success(``)
          setVisible(false)
        } else if (res.data.code === '20000') {
          message.success(``)
        }
      })
    setVisible(false)
  }

  const handleRegister = data => {
    if (data.cardId !== '-- --') {
    setVisible(true)
    } else {
      message.error(``)
    }
  }

  const handleDelete = data => {
    if (data.cardId !== '-- --') {
      axios
        .post(getDeleteUrl(), {
          type: getSceneId().education,
          cardId: data.cardId,
        })
        .then(res => {
          if (res.data.code === '10000') {
            message.success(``)
            props.setData({
              cardId: '-- --',
              courseName: '-- --',
              startTime: '-- --',
              endTime: '-- --',
              name: '-- --',
              studentCount: '-- --',
            })
            setVisible(false)
          } else if (res.data.code === '20000') {
            message.success(``)
          }
        })
    } else {
      message.error(``)
    }
  }

  return (
    <div className="education-control-wrap">
      <div className="btn-group">
        <Button className="edit" onClick={() => handleRegister(props.data)}>
          
        </Button>
        <Button className="edit" onClick={() => handleDelete(props.data)}>
          
        </Button>
      </div>
      <ul className="education-control">
        <li>
          <IconFont style={{ fontSize: '26px' }} type="icon-kapian" />
          <span className="education-text">{props.data.cardId}</span>
        </li>
        <li>
          <IconFont style={{ fontSize: '26px' }} type="icon-shuji" />
          <span className="education-text">
            {props.data.courseName}
          </span>
        </li>
        <li>
          <IconFont
            style={{ fontSize: '26px' }}
            type="icon-shijiankaishishijian"
          />
          <span className="education-text">
            {props.data.startTime}
          </span>
        </li>
        <li>
          <IconFont style={{ fontSize: '26px' }} type="icon-jieshushijian" />
          <span className="education-text">
            {props.data.startTime}
          </span>
        </li>
        <li>
          <IconFont style={{ fontSize: '26px' }} type="icon-yonghuxinxi" />
          <span className="education-text">{props.data.name}</span>
        </li>
        <li>
          <IconFont style={{ fontSize: '26px' }} type="icon-yonghu" />
          <span className="education-text">
            {props.data.studentCount}
          </span>
        </li>
      </ul>
      <CollectionCreateForm
        visible={visible}
        onCreate={onCreate}
        data={props.data}
        onCancel={() => {
          setVisible(false)
        }}
      />
    </div>
  )
}

export default EducationControl
