import React, { useEffect, useState } from 'react'
import './index.scss'
import { Form, Input, Button, Modal, message, Empty } from 'antd'
import {
  getSchoolListUrl,
  getUserToken,
  addSchoolListUrl,
  delSchoolListUrl,
} from '../../../api/api'
import axios from 'axios'
import { ExclamationCircleOutlined } from '@ant-design/icons'

const { confirm } = Modal

const CollectionCreateForm = ({ visible, onCreate, onCancel }) => {
  const [form] = Form.useForm()
  const formItemLayout = {
    labelCol: { span: 4 },
  }
  return (
    <Modal
      visible={visible}
      title=""
      onCancel={onCancel}
      okText=""
      cancelText=""
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            form.resetFields()
            onCreate(values)
          })
          .catch(info => {})
      }}
    >
      <Form
        {...formItemLayout}
        form={form}
        name="basic"
        initialValues={{ remember: true }}
      >
        <Form.Item
          label=""
          name="schoolName"
          rules={[{ required: true, message: '' }]}
        >
          <Input placeholder="" />
        </Form.Item>
        <Form.Item
          label=""
          name="remark"
          rules={[{ message: '' }]}
        >
          <Input placeholder="" />
        </Form.Item>
      </Form>
    </Modal>
  )
}

const StepTwo = () => {
  axios.defaults.headers.common['Authorization'] = getUserToken()

  const [schoolList, setschoolList] = useState([])

  useEffect(() => {
    getSchoolList()
  }, [])

  // 
  const getSchoolList = async () => {
    const result = await axios.get(getSchoolListUrl())
    if (result.data.code === '10000') {
      setschoolList(result.data.result.records)
    }
  }

  // 
  const [visible, setVisible] = useState(false)
  const onCreate = values => {
    axios
      .post(addSchoolListUrl(), {
        ...values,
      })
      .then(res => {
        if (res.data.code === '10000') {
          setVisible(false)
          getSchoolList()
          message.success(``)
        } else {
          message.error(``)
          setVisible(false)
        }
      })
  }

  const open = () => {
    setVisible(true)
  }

  const handleDelete = id => {
    confirm({
      title: '',
      icon: <ExclamationCircleOutlined />,
      content: '',
      okText: '',
      okType: 'danger',
      cancelText: '',
      onOk() {
        const userData = schoolList.find(item => item.id === id)
        axios.delete(delSchoolListUrl(userData.id)).then(res => {
          if (res.data.code === '10000') {
            setVisible(false)
            getSchoolList()
            message.success(``)
          }
        })
      },
      onCancel() {
        console.log('Cancel')
      },
    })
  }

  return (
    <div className="tab-two">
      <div className="btn-groups">
        <Button
          type="primary"
          onClick={() => {
            open()
          }}
        >
          
        </Button>
      </div>
      <div className="table-header">
        <span className="name"></span>
        <span className="desc"></span>
        <span className="edit-box"></span>
      </div>
      <ul className="user-list">
        {schoolList.length === 0 ? (
          <Empty />
        ) : (
          schoolList?.map(item => (
            <li key={item.id}>
              <span className="name">{item.schoolName}</span>
              <span className="desc">{item.remark}</span>
              <span className="edit-box">
                <Button
                  onClick={() => {
                    handleDelete(item.id)
                  }}
                  type="primary"
                >
                  
                </Button>
              </span>
            </li>
          ))
        )}
      </ul>
      <CollectionCreateForm
        visible={visible}
        onCreate={onCreate}
        onCancel={() => {
          setVisible(false)
        }}
      />
    </div>
  )
}

export default StepTwo
