import React, { useState } from 'react'
import './index.scss'
import { message, Modal, Form, Input, Button } from 'antd'
import axios from 'axios'
import { getRegisterUrl, getDeleteUrl, getUserToken, getSceneId } from '../../../api/api'

const CollectionCreateForm = ({ visible, onCreate, onCancel }) => {
  const [form] = Form.useForm()
  const handlePreinstall = () => {
    form.setFieldsValue({
      identityNumber: '420105200001014992',
      name: '',
      roomNumber: 'A104',
      stayCount: '3',
      prePaid: '200'
    })
  }
  return (
    <Modal
      visible={visible}
      title=""
      onCancel={onCancel}
      okText=""
      cancelText=""
      onOk={() => {
        form
          .validateFields()
          .then(values => {
            form.resetFields()
            onCreate(values)
          })
          .catch(info => {
            console.log('Validate Failed:', info)
          })
      }}
    >
      <Form form={form} name="basic" initialValues={{ remember: true }}>
        <Button size="small" type="primary" className="preinstall" onClick={() => handlePreinstall()}></Button>
        <Form.Item
          label=""
          name="identityNumber"
          rules={[
            {
              required: true,
              pattern: new RegExp(
                /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/,
                'g'
              ),
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="name"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="roomNumber"
          rules={[{ required: true, message: '' }]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="stayCount"
          rules={[
            {
              required: true,
              pattern: new RegExp(/^[1-9]\d*$/, 'g'),
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
        <Form.Item
          label=""
          name="prePaid"
          rules={[
            {
              required: true,
              pattern: new RegExp(/^[1-9]\d*$/, 'g'),
              message: '',
            },
          ]}
        >
          <Input />
        </Form.Item>
      </Form>
    </Modal>
  )
}

const HotelPanel = props => {
  axios.defaults.headers.common['Authorization'] = getUserToken()
  const [visible, setVisible] = useState(false)

  const onCreate = values => {
    const data = { ...values, cardId: props.data.cardId }
    axios.post(getRegisterUrl(), {
      type: getSceneId().hotel,
      value: {
        ...data,
      },
      cardId: props.data.cardId,
    }).then(res => {
      if (res.data.code === '10000') {
        message.success(``)
        setVisible(false)
      } else if (res.data.code === '20000') {
        message.success(``)
      }
    })
  }

  const handleRegister = data => {
    if (data.cardId !== '-- --') {
    setVisible(true)
    } else {
      message.error(``)
    }
  }

  const handleDelete = data => {
    if (data.cardId !== '-- --') {
      axios.post(getDeleteUrl(), {
        type: getSceneId().hotel,
        cardId: data.cardId,
      }).then(res => {
        if (res.data.code === '10000') {
          message.success(``)
          props.setData({
            cardId: '-- --',
            identityNumber: '-- --',
            name: '-- --',
            time: '-- --',
            stayCount: '-- --',
            prePaid: '-- --',
            roomNumber: '-- --',
          })
          setVisible(false)
        } else if (res.data.code === '20000') {
          message.success(``)
        }
      })
    } else {
      message.error(``)
    }
  }

  return (
    <div className="hotel-panel-wrap">
      <div className="btn-group">
        <Button onClick={() => handleRegister(props.data)}></Button>
        <Button onClick={() => handleDelete(props.data)}></Button>
      </div>
      <ul>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.cardId}</span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">
            {props.data.identityNumber}
          </span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.name}</span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.time}</span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.stayCount}</span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.prePaid}</span>
        </li>
        <li>
          <span className="title"></span>
          <span className="information-content">{props.data.roomNumber}</span>
        </li>
      </ul>
      <CollectionCreateForm
        visible={visible}
        onCreate={onCreate}
        onCancel={() => {
          setVisible(false)
        }}
      />
    </div>
  )
}

export default HotelPanel
